/* CSS RESET */
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

/* GLOBAL STYLES */
body {
    background-color: #111518;
    font-family: Arial, Helvetica, sans-serif;
}

.container {
    max-width: 1200px;
    margin: 0 auto 50px auto;
}

/* CLASSLISTS */
.hideAll {
    display: none;
}